/* Write your CSS here */
.main_container {
  background-image: linear-gradient(to right, #defafe, #cffcf1, #00d9f5);
  margin: none;

  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  box-shadow: 0px 4px 16px 0px;
  background-color: #00d9f5;
}
.heading {
  color: #0f172a;
}
.sub_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.div_1 {
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url('https://assets.ccbp.in/frontend/react-js/digital-timer-elapsed-bg.png');
  background-size: cover;
  background-position: center;
  height: 300px;
}
.div_1_1 {
  background-color: white;
  border: white;
  width: 100px;
  height: 100px;
  border-radius: 200px;
  text-align: center;
  display: flex;
  flex-direction: column;
}
.timer_1 {
  color: #00d9f5;
  margin-bottom: 1px;
  padding: 0px;
}
.p1 {
  font-weight: bold;
  margin: 0px;
  padding: 0px;
}
.div_2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.div_2_1 {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.Running_img {
  width: 20px;
}
button {
  border: none;
  background-color: transparent;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  margin: 0px;
  margin-right: 10px;
}
.running_heading {
  margin-right: 20px;
}
.reset_img {
  width: 20px;
}
.reset {
}
.minus_button {
  font-weight: bold;
  font-size: 20px;
}
.plus_button {
  font-weight: bold;
  font-size: 20px;
}
.div_2_2_2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 50%;
}
.timer_2 {
  background-image: linear-gradient(to right, #cffcf1, #00d9f5);
  width: 60px;
  height: 40px;

  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  border-radius: 10px;
}
.timer_h {
  padding: 0px;
  margin: 0px;
  font-size: 20px;
}
p {
  color: #1e293b;
  font-weight: bold;
}
